.input
    min-width: 250px
    border: 0.06rem solid darkgrey
    outline: none
    padding: 0.45rem 0.25rem
    font-size: inherit
    font-family: var(--font-info)
    border-radius: 0.25rem
    transition-property: border, box-shadow
    transition-duration: var(--animation-interactive-time)
    transition-timing-function: var(--animation-interactive-function)
    &:focus
        border-color: transparent
        box-shadow: var(--focus)
