.header
    display: flex
    justify-content: space-between
    align-items: center
    flex-wrap: wrap
    transition-property: background-color
    transition-duration: var(--animation-ui-interactive-time)
    transition-timing-function: var(--animation-ui-interactive-function)

@media screen and (max-width: 900px)
    .header
        justify-content: space-around
