.main__row
    display: flex
    flex-wrap: wrap
    justify-content: space-between
    margin-bottom: 2rem
    transition-property: color
    transition-duration: var(--animation-ui-interactive-time)
    transition-timing-function: var(--animation-ui-interactive-function)

@media screen and (max-width: 700px)
    .main__row
        justify-content: space-evenly
