.button
    border: 0.06rem solid darkgrey
    outline: none
    padding: 0.75rem 0.5rem
    font-size: 1rem
    font-family: var(--font-info)
    border-radius: 0.25rem
    cursor: pointer
    user-select: none
    transition-property: transform, border, box-shadow
    transition-duration: var(--animation-interactive-time)
    transition-timing-function: var(--animation-interactive-function)
    &:hover
        transform: scale(1.1)
    &:focus
        border-color: transparent
        box-shadow: var(--focus)
    &:active
        box-shadow: var(--active)

